import axios from 'axios';
import { toast } from 'react-toastify';

const API_BASE_URL = process.env.REACT_APP_API_URL || '/api';

const apiClient = axios.create({
    baseURL: API_BASE_URL,
    headers: {
        'Content-Type': 'application/json'
    }
});

// Request interceptor - Token ekle
apiClient.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem('vendorPortalToken');
        if (token) {
            config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// Response interceptor - Hata ynetimi
apiClient.interceptors.response.use(
    (response) => {
        return response;
    },
    (error) => {
        if (error.response) {
            // Server yant verdi ama hata kodu dnd
            const { status, data } = error.response;

            if (status === 401) {
                // Unauthorized - Token geersiz veya yok
                localStorage.removeItem('vendorPortalToken');
                window.location.href = '/login';
                toast.error('Oturumunuz sona erdi. Ltfen tekrar giri yapn.');
            } else if (status === 403) {
                toast.error('Bu ilem iin yetkiniz bulunmamaktadr.');
            } else if (status === 404) {
                toast.error('stenen kaynak bulunamad.');
            } else if (status === 500) {
                toast.error('Sunucu hatas olutu. Ltfen daha sonra tekrar deneyin.');
            } else {
                toast.error(data?.message || 'Bir hata olutu.');
            }
        } else if (error.request) {
            // stek gnderildi ama yant alnamad
            toast.error('Sunucuya balanlamad. nternet balantnz kontrol edin.');
        } else {
            // Baka bir hata
            toast.error('Beklenmeyen bir hata olutu.');
        }

        return Promise.reject(error);
    }
);

// ========== API METHODS ==========

