import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { Card, Form, Button, Spinner } from 'react-bootstrap';
import { toast } from 'react-toastify';
import { malzemeApi, firmaApi } from '../../api/apiClient';

const MalzemeEdit = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const [firmalar, setFirmalar] = useState([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [formData, setFormData] = useState({
    id: '',
    firmaId: '',
    kod: '',
    ad: '',
    aciklama: '',
    birim: 'Adet',
    fiyat: '',
    paraBirimi: 'TRY'
  });

  useEffect(() => {
    loadFirmalar();
    loadMalzeme();
  }, [id]);

  const loadFirmalar = async () => {
    try {
      const response = await firmaApi.getAll();
      setFirmalar(response.data.data || []);
    } catch (error) {
      console.error('Firmalar yklenirken hata:', error);
    }
  };

  const loadMalzeme = async () => {
    try {
      const response = await malzemeApi.getById(id);
      const malzeme = response.data.data;
      setFormData({
        id: malzeme.id,
        firmaId: malzeme.firmaId || '',
        kod: malzeme.kod || '',
        ad: malzeme.ad || '',
        aciklama: malzeme.aciklama || '',
        birim: malzeme.birim || 'Adet',
        fiyat: malzeme.fiyat || '',
        paraBirimi: malzeme.paraBirimi || 'TRY'
      });
    } catch (error) {
      toast.error('Malzeme bilgileri yklenirken hata olutu.');
      navigate('/malzeme');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);

    try {
      const data = {
        ...formData,
        fiyat: formData.fiyat ? parseFloat(formData.fiyat) : null
      };
      
      await malzemeApi.update(data);
      toast.success('Malzeme baaryla gncellendi!');
      navigate('/malzeme');
    } catch (error) {
      toast.error('Malzeme gncellenirken hata olutu.');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center" style={{ minHeight: '400px' }}>
        <Spinner animation="border" variant="primary" />
      </div>
    );
  }

  return (
    <div>
      <div className="mb-4">
        <h2>
          <i className="fas fa-edit me-2"></i>
          Malzeme Dzenle
        </h2>
      </div>

      <Card className="shadow">
        <Card.Body>
          <Form onSubmit={handleSubmit}>
            <Form.Group className="mb-3">
              <Form.Label>Firma *</Form.Label>
              <Form.Select
                value={formData.firmaId}
                onChange={(e) => setFormData({ ...formData, firmaId: e.target.value })}
                required
              >
                <option value="">Firma seiniz...</option>
                {firmalar.map(firma => (
                  <option key={firma.id} value={firma.id}>
                    {firma.ad}
                  </option>
                ))}
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Malzeme Kodu *</Form.Label>
              <Form.Control
                type="text"
                value={formData.kod}
                onChange={(e) => setFormData({ ...formData, kod: e.target.value })}
                required
                placeholder="MLZ001"
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Malzeme Ad *</Form.Label>
              <Form.Control
                type="text"
                value={formData.ad}
                onChange={(e) => setFormData({ ...formData, ad: e.target.value })}
                required
                placeholder="Malzeme adn giriniz"
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Aklama</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                value={formData.aciklama}
                onChange={(e) => setFormData({ ...formData, aciklama: e.target.value })}
                placeholder="Malzeme aklamas (opsiyonel)"
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Birim</Form.Label>
              <Form.Select
                value={formData.birim}
                onChange={(e) => setFormData({ ...formData, birim: e.target.value })}
              >
                <option value="Adet">Adet</option>
                <option value="KG">KG</option>
                <option value="Gram">Gram</option>
                <option value="Ton">Ton</option>
                <option value="Metre">Metre</option>
                <option value="Litre">Litre</option>
                <option value="M2">M</option>
                <option value="M3">M</option>
                <option value="Paket">Paket</option>
                <option value="Kutu">Kutu</option>
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Fiyat</Form.Label>
              <Form.Control
                type="number"
                step="0.01"
                value={formData.fiyat}
                onChange={(e) => setFormData({ ...formData, fiyat: e.target.value })}
                placeholder="0.00"
              />
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label>Para Birimi</Form.Label>
              <Form.Select
                value={formData.paraBirimi}
                onChange={(e) => setFormData({ ...formData, paraBirimi: e.target.value })}
              >
                <option value="TRY">TRY ()</option>
                <option value="USD">USD ($)</option>
                <option value="EUR">EUR ()</option>
                <option value="GBP">GBP ()</option>
              </Form.Select>
            </Form.Group>

            <div className="d-grid gap-2">
              <Button 
                variant="primary" 
                type="submit"
                disabled={saving}
              >
                {saving ? (
                  <>
                    <Spinner animation="border" size="sm" className="me-2" />
                    Gncelleniyor...
                  </>
                ) : (
                  <>
                    <i className="fas fa-save me-2"></i>
                    Gncelle
                  </>
                )}
              </Button>
              <Button 
                variant="secondary" 
                onClick={() => navigate('/malzeme')}
                disabled={saving}
              >
                <i className="fas fa-arrow-left me-2"></i>
                ptal
              </Button>
            </div>
          </Form>
        </Card.Body>
      </Card>
    </div>
  );
};

export default MalzemeEdit;
