import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Button, Table, Badge, Spinner } from 'react-bootstrap';
import { toast } from 'react-toastify';
import { ihaleApi } from '../../api/apiClient';
import { useAuth } from '../../context/AuthContext';

const IhaleList = () => {
  const [ihaleler, setIhaleler] = useState([]);
  const [loading, setLoading] = useState(true);
  const { hasRole } = useAuth();

  useEffect(() => {
    loadIhaleler();
  }, []);

  const loadIhaleler = async () => {
    try {
      const response = await ihaleApi.getAll();
      setIhaleler(response.data.data || []);
    } catch (error) {
      toast.error('haleler yklenirken hata olutu.');
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm('Bu ihaleyi silmek istediinizden emin misiniz?')) {
      return;
    }

    try {
      await ihaleApi.delete(id);
      toast.success('hale baaryla silindi.');
      loadIhaleler();
    } catch (error) {
      toast.error('hale silinirken hata olutu.');
    }
  };

  const handleYayinla = async (id) => {
    if (!window.confirm('Bu ihaleyi yaynlamak istediinizden emin misiniz?')) {
      return;
    }

    try {
      await ihaleApi.yayinla(id);
      toast.success('hale baaryla yaynland!');
      loadIhaleler();
    } catch (error) {
      toast.error('hale yaynlanrken hata olutu.');
    }
  };

  const getIhaleTuruText = (tur) => {
    switch (tur) {
      case 1: return <Badge bg="info">Herkese Ak</Badge>;
      case 2: return <Badge bg="warning">Davetli</Badge>;
      case 3: return <Badge bg="secondary">Kapal</Badge>;
      default: return '-';
    }
  };

  const getDurumText = (durum) => {
    switch (durum) {
      case 1: return <Badge bg="secondary">Taslak</Badge>;
      case 2: return <Badge bg="success">Yaynda</Badge>;
      case 3: return <Badge bg="primary">Tamamland</Badge>;
      case 4: return <Badge bg="danger">ptal</Badge>;
      default: return '-';
    }
  };

  return (
    <div>
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2>
          <i className="fas fa-gavel me-2"></i>
          hale Ynetimi
        </h2>
        {(hasRole('Admin') || hasRole('Musteri')) && (
          <Link to="/ihale/create" className="btn btn-primary">
            <i className="fas fa-plus me-2"></i>
            Yeni hale A
          </Link>
        )}
      </div>

      <div className="card shadow">
        <div className="card-body">
          {loading ? (
            <div className="text-center py-5">
              <Spinner animation="border" variant="primary" />
              <p className="mt-2">Ykleniyor...</p>
            </div>
          ) : (
            <div className="table-responsive">
              <Table hover>
                <thead className="table-dark">
                  <tr>
                    <th>hale Ad</th>
                    <th>Referans No</th>
                    <th>Mteri Firma</th>
                    <th>hale Tr</th>
                    <th>Durum</th>
                    <th>Balang</th>
                    <th>Biti</th>
                    <th>lemler</th>
                  </tr>
                </thead>
                <tbody>
                  {ihaleler.length > 0 ? (
                    ihaleler.map((ihale) => (
                      <tr key={ihale.id}>
                        <td>
                          <strong>{ihale.ihaleAdi}</strong>
                        </td>
                        <td>{ihale.referansNo}</td>
                        <td>{ihale.musteriFirma?.ad || '-'}</td>
                        <td>{getIhaleTuruText(ihale.ihaleTuru)}</td>
                        <td>{getDurumText(ihale.durum)}</td>
                        <td>
                          {ihale.teklifBaslangicTarihi 
                            ? new Date(ihale.teklifBaslangicTarihi).toLocaleDateString('tr-TR')
                            : '-'}
                        </td>
                        <td>
                          {ihale.teklifBitisTarihi 
                            ? new Date(ihale.teklifBitisTarihi).toLocaleDateString('tr-TR')
                            : '-'}
                        </td>
                        <td>
                          <Link 
                            to={`/ihale/details/${ihale.id}`}
                            className="btn btn-sm btn-info me-2"
                            title="Detay"
                          >
                            <i className="fas fa-eye"></i>
                          </Link>
                          
                          {ihale.durum === 1 && (hasRole('Admin') || hasRole('Musteri')) && (
                            <>
                              <Link 
                                to={`/ihale/edit/${ihale.id}`}
                                className="btn btn-sm btn-warning me-2"
                                title="Dzenle"
                              >
                                <i className="fas fa-edit"></i>
                              </Link>
                              <Button
                                variant="success"
                                size="sm"
                                className="me-2"
                                onClick={() => handleYayinla(ihale.id)}
                                title="Yaynla"
                              >
                                <i className="fas fa-rocket"></i>
                              </Button>
                            </>
                          )}

                          {ihale.durum === 2 && (
                            <Link 
                              to={`/ihale/teklifler/${ihale.id}`}
                              className="btn btn-sm btn-primary me-2"
                              title="Teklifler"
                            >
                              <i className="fas fa-file-invoice-dollar"></i>
                            </Link>
                          )}

                          {hasRole('Admin') && (
                            <Button
                              variant="danger"
                              size="sm"
                              onClick={() => handleDelete(ihale.id)}
                              title="Sil"
                            >
                              <i className="fas fa-trash"></i>
                            </Button>
                          )}
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="8" className="text-center py-4">
                        <i className="fas fa-inbox fa-3x text-muted mb-3 d-block"></i>
                        Henz ihale eklenmemi.
                      </td>
                    </tr>
                  )}
                </tbody>
              </Table>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default IhaleList;
